<template>
	<div>
		<mt-header v-if='isHeaderShow' fixed title="">
			<mt-button slot="left" @click="goBack()"><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
			<mt-button @click.native="choose()" type="danger" slot="right"></mt-button>
		</mt-header>
		<div class="flex">
			<span style="display:none;">/</span>
			<calendar :range="calendar2.range" :lunar="calendar2.lunar" :value="calendar2.value" :begin="calendar2.begin" :end="calendar2.end" @select="calendar2.select"></calendar>
		</div>
		<div v-if='isBottomShow' class="dateSelect">
			<mt-button @click.native="resetData()" type="default"></mt-button>
			<mt-button @click.native="confirm()" type="danger"></mt-button>
		</div>
	</div>
</template>

<script>
	import calendar from '@/components/calendar.vue'

	export default {
		name: 'calendarSelect',
		components: {
			calendar
		},
		props: {
			showTopOrBottom: 0
		},
		data() {
			return {
				startTime: '',
				endTime: '',
				startValue: [],
				endValue: [],
				calendar2: {
					range: true,
					value: [
						[2017, 2, 16],
						[2019, 2, 16]
					], //
					lunar: true, //
					//        begin:[2017,2,16], //
					//        end:[2019,2,16], //
					select: function(begin, end) {
						var beginYear = begin[0];
						var beginMonth = begin[1];
						var beginDay = begin[2];
						var endYear = end[0];
						var endMonth = end[1];
						var endDay = end[2];
						beginMonth++;
						endMonth++;
						this.startTime = beginYear + '/' + beginMonth + '/' + beginDay;
						this.endTime = endYear + '/' + endMonth + '/' + endDay;
					}.bind(this)
				},
				isHeaderShow: true,
				isBottomShow: false,
			}
		},
		methods: {
			goBack: function() {
				this.$router.go(-1);
			},
			//
			choose: function() {
				var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
				var startTime = this.startTime;
				var endTime = this.endTime;
				clueInfo.startTime = startTime;
				clueInfo.endTime = endTime;
				window.localStorage.setItem("clueInfo", JSON.stringify(clueInfo));
				this.$router.go(-1);
			},

			//
			resetData: function() {
				//	  		var startTime = this.startTime;
				//	    	var endTime = this.endTime;
				//	    	this.$emit('departmentChosen',startTime,endTime)
			},
			//
			confirm: function() {
				var startTime = this.startTime;
				var endTime = this.endTime;
				this.$emit('dateChosen', startTime, endTime)
			},
		},
		created: function() {
			if(this.showTopOrBottom == 1) {
				this.isHeaderShow = false;
				this.isBottomShow = true;
			} else {
				this.isHeaderShow = true;
				this.isBottomShow = false;
			}

			var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
			var d = new Date();
			var year = d.getFullYear();
			var month = d.getMonth();
			var day = d.getDate();
			if(clueInfo != null && clueInfo.startTime != null && clueInfo.startTime != '' && clueInfo.startTime != '') {
				//				this.startTime = clueInfo.startTime;
				this.startValue = clueInfo.startTime.split('/');
			} else {
				this.startValue = [year, month, day];
			}
			if(clueInfo != null && clueInfo.endTime != null && clueInfo.endTime != '' && clueInfo.endTime != '') {
				//				this.endTime = clueInfo.endTime;
				this.endValue = clueInfo.endTime.split('/');
			} else {
				this.endValue = [year, month, day];
			}
			this.calendar2.value = [this.startValue, this.endValue];
		},
	}
</script>

<style>
	.return {
		vertical-align: middle;
		margin-right: 10px;
	}
	/*demo*/
	
	.flex {
		box-sizing: border-box;
		display: -webkit-box;
		-webkit-box-pack: start;
		-webkit-box-align: start;
		display: -webkit-flex;
		-webkit-justify-content: space-between;
		-webkit-align-items: top;
		display: flex;
		justify-content: space-between;
		align-items: top;
		flex-flow: row wrap
	}
	
	.flex>div {
		margin: 10px;
		padding: 20px;
		width: 25%;
		min-width: 300px;
		border: 1px solid #eee;
		border-radius: 2px;
		position: relative;
	}
	
	.flex>div>span {
		position: absolute;
		left: 0px;
		top: 0px;
		padding: 2px 10px;
		font-size: 10px;
		border-radius: 0 0 2px 0;
		background: #ea6151;
		color: #fff;
	}
	
	.flex>div>input {
		box-sizing: border-box;
		width: 100%;
		margin-top: 20px;
		border-radius: 2px;
		border: 1px solid #dedede;
		padding: 10px;
		font-size: 16px;
		background: url(data:image/svg+xml;base64,) no-repeat 8px 10px;
		padding-left: 36px;
		color: #666;
	}
	/*transition*/
	
	.fade-enter-active,
	.fade-leave-active {
		transition: all .5s ease-in-out;
	}
	
	.fade-enter,
	.fade-leave-active {
		opacity: 0;
		transform: translateY(-10px);
	}
	/**/
	
	.calendar-dropdown {
		background: #fff;
		position: absolute;
		left: 0;
		top: 0;
		padding: 20px;
		border: 1px solid #eee;
		border-radius: 2px;
	}
	
	.calendar-dropdown:before {
		position: absolute;
		left: 30px;
		top: -10px;
		content: "";
		border: 5px solid rgba(0, 0, 0, 0);
		border-bottom-color: #DEDEDE;
	}
	
	.calendar-dropdown:after {
		position: absolute;
		left: 30px;
		top: -9px;
		content: "";
		border: 5px solid rgba(0, 0, 0, 0);
		border-bottom-color: #fff;
	}
	/**/
	
	.calendar-dialog {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
	}
	
	.calendar-dialog-mask {
		background: rgba(255, 255, 255, .5);
		width: 100%;
		height: 100%;
	}
	
	.calendar-dialog-body {
		background: #fff;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		padding: 20px;
		border: 1px solid #eee;
		border-radius: 2px;
	}
	
	.dateSelect {
		display: flex;
		position: fixed;
		width: 100%;
		left: 0;
		bottom: 0;
		z-index: 9999;
	}
	
	.dateSelect button {
		flex: 1;
		border-radius: 0;
		font-size: 14px;
	}
	
	.flex {
		padding-bottom: 40px;
	}
</style>


