<template>
	<div v-if="isShow" class="userSelectItem">
		<div class="header">
			<mt-header fixed title="">
				<mt-button slot="left" icon="back" @click="goBack()"></mt-button>
			</mt-header>
		</div>
		<mt-search class="searchArea" v-model="selectValue" :show=true>
			<p></p>
			<ul>
				<li @click="chooseLi(0,index,item.userId)" v-for="(item,index) in selectedUser">
					<span>{{item.userName}}</span>
					<span>{{item.orgName}}</span>
					<span v-show="item.isSelect"></span>
				</li>
			</ul>
			<p></p>
			<ul>
				<li @click="chooseLi(1,index,item.userId)" v-for="(item,index) in result">
					<span>{{item.userName}}</span>
					<span>{{item.orgName}}</span>
					<span v-show="item.isSelect"></span>
				</li>
			</ul>
			<!--<mt-cell v-for="item in result" :title="item.title" :value="item.value">
			</mt-cell>-->
		</mt-search>
		<div class="userSelect">
			<mt-button @click.native="confirm()" size="large" type="danger"></mt-button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'userSelect',
		data() {
			return {
				selectValue: '',
				selectedUser: [],
				result: [],
				selectTopicGuid: '',
				isShow: false
			}
		},
		created: function() {
//			this.selectedUser = [{
//					userId: '1',
//					userName: '',
//					orgName: '',
//					isSelect: true
//				},
//				{
//					userId: '2',
//					userName: '1',
//					orgName: '',
//					isSelect: true
//				}
//			];
		},
		methods: {
			show: function(selectedUser, selectTopicGuid) {
				this.isShow = true;
				this.selectedUser = selectedUser;
				this.selectTopicGuid = selectTopicGuid;
			},
			chooseLi: function(type,index,userId) {
				if(type == 0){
					if(this.selectedUser[index].isSelect) {
						this.selectedUser[index].isSelect = false;
					} else {
						this.selectedUser[index].isSelect = true;
					}
					for(var i in this.result){
						if(userId == this.result[i].userId){
							if(this.result[i].isSelect) {
								this.result[i].isSelect = false;
							} else {
								this.result[i].isSelect = true;
							}
							break;
						}
					}
				}else{
					if(this.result[index].isSelect) {
						this.result[index].isSelect = false;
					} else {
						this.result[index].isSelect = true;
					}
					var isContain = false;
					for(var i in this.selectedUser){
						if(userId == this.selectedUser[i].userId){
							if(this.selectedUser[i].isSelect) {
								this.selectedUser[i].isSelect = false;
							} else {
								this.selectedUser[i].isSelect = true;
							}
							isContain = true;
							break;
						}
					}
					if(!isContain){
						var userInfo = {
							userId: this.result[index].userId,
							userName: this.result[index].userName,
							orgName: this.result[index].orgName,
							isSelect: true
						}
						this.selectedUser.push(userInfo);
					}
					
				}
			},
			goBack: function() {
				this.isShow = false;
			},
			confirm: function(){
				var selectedUserId = '';
				for(var i in this.selectedUser){
					if(this.selectedUser[i].isSelect == true){
						if(i == this.selectedUser.length - 1) {
							selectedUserId += this.selectedUser[i].userId;
						} else {
							selectedUserId += this.selectedUser[i].userId+',';
						}
					}
				}
				this.axios.post('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/addSelect', {
					'id': this.selectTopicGuid,
					'membersId':selectedUserId
				})
				.then(function(response) {
					var message = '';
					if(response.data.commonResponse.success){
						message = '';
					}
					alert(message);
					this.isShow = false;
				}.bind(this));
			},
		},
		watch: {
			selectValue(curVal) {
				var data = {
					"userName": curVal,
				}
				this.axios.post('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/user/getUsers?except=true', data).then(function(response) {
					if(response.data.commonResponse.success) {
						this.result = [];
						for(var i in response.data.records) {
							var userInfo = response.data.records[i];
							var userInfo = {
								userId: response.data.records[i].id,
								userName: response.data.records[i].username,
								orgName: response.data.records[i].orgName,
								isSelect: false
							}
							this.result.push(userInfo);
						}
					}
				}.bind(this))
			}
		}
	}
</script>

<style>
	.userSelect {
		display: flex;
		position: fixed;
		width: 100%;
		left: 0;
		bottom: 0;
	}
	
	.userSelect button {
		flex: 1;
		border-radius: 0;
		font-size: 14px;
	}
	
	.userSelectItem{
		position: fixed;
		/*top: 40px;*/
		top: 0;
		background: #FFFFFF;
		width: 102%;
		height: 100%;
		overflow: auto;
		z-index: 99999999;
	}
</style>
<style scoped>
	.searchArea{
		margin-top: 40px;
	}
</style>


