<template>
	<div v-if="isShow" class="departmentitem">
		<mt-header v-if='isHeaderShow' fixed title="">
			<mt-button slot="left" @click="goBack()"><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
			<mt-button @click.native="choose()" type="danger" slot="right"></mt-button>
		</mt-header>
		<!--<h1>Hello Ztree()</h1>-->
		<div v-if='ztreeDataSource.length>0' :class="{'treeContainer':treeContainer,'resizeHeader':resizeHeader}">
			<vue-ztree :list.sync='ztreeDataSource' :is-open='false'></vue-ztree>
		</div>
		<div v-if='isBottomShow' class="departSelect">
			<mt-button @click.native="resetData()" type="default"></mt-button>
			<mt-button @click.native="confirm()" type="danger"></mt-button>
		</div>
	</div>
	<!--<div style='flex:1'>
        <h1>Hello Ztree()</h1>
        <div style='width:280px;' v-if='ztreeDataSourceSync.length>0'>
           <vue-ztree :list.sync='ztreeDataSourceSync' :func='nodeClick' :expand='expandClick'  :is-open='false'></vue-ztree>
        </div>
      </div>
      <div style='flex:1'>
        <h1>Hello Ztree()</h1>
        <div style='width:280px;' v-if='ztreeDataSourceList.length>0'>
           <vue-ztree :list.sync='ztreeDataSourceList' :contextmenu='contextmenuClick' :is-open='true'></vue-ztree>
        </div>
      </div>-->
</template>

<script>
	import vueZtree from '@/components/vue-ztree.vue'
	export default {
		name: 'tree',
		props: {
			showTopOrBottom: 0,
			isDetail: false,
		},
		data() {
			return {
				msg: 'Hello Vue-Ztree!',
				ztreeDataSource: [],
				ztreeDataSourceSync: [],
				isHeaderShow: true,
				isBottomShow: false,
				treeContainer: true,
				resizeHeader: false,
				isShow: false,
			}
		},
		methods: {
			// 
			nodeClick: function(m) {
				console.log(JSON.parse(JSON.stringify(m)));
			},
			// 
			contextmenuClick: function() {
				console.log("contextmenuClick");
			},
			// 
			expandClick: function(m) {
				console.log(JSON.parse(JSON.stringify(m)));
			},
			//
			choose: function() {
				var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
				var orgId = [];
				var orgName = [];
				var curSelectedNode = document.getElementsByClassName('curSelectedNode');
				for(var i = 0; i < curSelectedNode.length; i++) {
					orgId.push(curSelectedNode[i].getElementsByClassName('node_id')[0].innerHTML);
					orgName.push(curSelectedNode[i].getElementsByClassName('node_name')[0].innerHTML);
				}
				clueInfo.orgId = orgId;
				clueInfo.orgName = orgName;
				window.localStorage.setItem("clueInfo", JSON.stringify(clueInfo));
				this.$router.go(-1);
			},
			//
			goBack: function() {
				if(this.isDetail) {

				} else {
					this.$router.go(-1);
				}
			},
			//
			resetData: function() {
				var curSelectedNode = document.getElementsByClassName('curSelectedNode');
				for(var i = 0; i < curSelectedNode.length; i++) {
					curSelectedNode[i].className = curSelectedNode[i].className.replace('curSelectedNode', '');
				}
				this.$emit('departmentChosen', '', '')
			},
			//
			confirm: function() {
				var orgId = [];
				var orgName = [];
				var curSelectedNode = document.getElementsByClassName('curSelectedNode');
				for(var i = 0; i < curSelectedNode.length; i++) {
					orgId.push(curSelectedNode[i].getElementsByClassName('node_id')[0].innerHTML);
					orgName.push(curSelectedNode[i].getElementsByClassName('node_name')[0].innerHTML);
				}
				orgName = orgName + '';
				if(orgName.length > 5) {
					orgName = orgName.substring(0, 5) + '...';
				}
				this.$emit('departmentChosen', orgId + '', orgName + '')
			},
			//
			show: function(){
				
			},
		},
		components: {
			vueZtree
		},
		created() {
			//
			if(isDetail == false){
				isShow = true;
			}
			
			if(this.showTopOrBottom == 1) {
				this.isHeaderShow = false;
				this.isBottomShow = true;
				this.resizeHeader = true;
			} else {
				this.isHeaderShow = true;
				this.isBottomShow = false;
			}

			var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
			var orgId = [];
			if(clueInfo != null) {
				if(clueInfo.orgId != null) {
					orgId = clueInfo.orgId.split(',');
				}
			}

			var instance = this.axios.create();

			instance.post(
				"http://192.168.8.241/cre/api/authorization/organization/load/mainclass", {
					userId: '8ca66580fa6b42fe9e625f6202fc2192',
					privilegePrefix: 'PID_NEWSCOMMAND_CLUE_BROWSE_RELATED'
				}, {
					headers: {
						'Content-Type': 'text/plain;utf-8'
					}
				}
			).then(function(response) {
				var treeData = response.data.organizationList;
				var zTreeData1 = transFormZtreeData(treeData);
				this.ztreeDataSource = zTreeData1;
			}.bind(this))

			/**
			 * 
			 * @param {Object} organizationList
			 */
			function transFormZtreeData(organizationList) {

				/**
				 * textnameisNeedTraversal
				 */
				var departmentList = [];
				for(var i in organizationList) {
					var isOpen = false;
					var isClickNode = false;
					for(var j in orgId) {
						if(organizationList[i].id == orgId[j]) {
							isOpen = true;
							isClickNode = true;
							break;
						}
					}
					//  				if(organizationList[i].id == 'CBD0FFBB-49AF-4FF7-A3AA-49B9881F87D9'){
					//  						isOpen = true;
					//  						isClickNode = true;
					//						}
					var department = {
						id: organizationList[i].id,
						name: organizationList[i].text,
						parentId: organizationList[i].parentId,
						isNeedTraversal: true,
						isOpen: isOpen,
						open: false,
						clickNode: isClickNode,
					}
					departmentList.push(department);
				}

				/**
				 * 
				 * @param {Object} zTreeData1
				 * @param {Object} department
				 * @param {Object} isChildren
				 */
				var traversal = function(zTreeData1, department, isChildren) {
					for(var j = 0; j < zTreeData1.length; j++) {
						if(zTreeData1[j].isNeedTraversal == false) {
							continue;
						}
						if(department.parentId == zTreeData1[j].id) {
							if(zTreeData1[j].children == null) {
								zTreeData1[j].children = [];
							}
							zTreeData1[j].children.push(department);
							isChildren = true;
							break;
						}
						if(department.id == zTreeData1[j].parentId) {
							if(department.children == null) {
								department.children = [];
							}
							if(zTreeData1[j].isOpen == true) {
								department.open = true;
								department.isOpen = true;
							}
							department.children.push(zTreeData1[j]);
							zTreeData1.push(department);
							zTreeData1[j].isNeedTraversal = false;
							isChildren = true;
						}
						if(zTreeData1[j].children != null) {
							traversal(zTreeData1[j].children, department, isChildren);
						}
					}
				}

				/**
				 * 
				 */
				var zTreeData = [];
				for(var i in departmentList) {
					var isChildren = false;
					traversal(zTreeData, departmentList[i], isChildren);
					if(!isChildren) {
						zTreeData.push(departmentList[i]);
					}
				}

				/**
				 * 
				 */
				var zTreeData1 = [];
				for(var i in zTreeData) {
					if(zTreeData[i].isNeedTraversal == true) {
						var isPush = true;
						for(var j in zTreeData1) {
							if(zTreeData1[j].id == zTreeData[i].id) {
								isPush = false;
								break;
							}
						}
						if(isPush) {
							zTreeData1.push(zTreeData[i])
						}
					}
				}
				return zTreeData1;
			}
		}
	}
</script>
<style type="text/css">
	.departmentitem {
		position: fixed;
		/*top: 40px;*/
		top: 0;
		background: #FFFFFF;
		width: 102%;
		height: 100%;
		overflow: auto;
		z-index: 99999999;
	}
</style>
<style scoped>
	/* body {font-family: Helvetica, sans-serif;}*/
	
	.return {
		vertical-align: middle;
		margin-right: 10px;
	}
	
	.iconClassRoot {
		width: 15px;
		height: 15px;
		display: inline-block;
		background: url("../images/ztree/root.png") no-repeat center/100% auto;
	}
	
	.iconClassNode {
		width: 15px;
		height: 15px;
		display: inline-block;
		background: url("../images/ztree/node.png") no-repeat center/100% auto;
	}
	
	.treeContainer {
		margin-top: 40px;
		width: 100%;
	}
	
	.resizeHeader {
		margin-top: 0;
	}
	
	ul.ztree {
		border: 0;
	}
	
	.departSelect {
		display: flex;
		position: fixed;
		width: 100%;
		left: 0;
		bottom: 0;
		z-index: 9999;
	}
	
	.departSelect button {
		flex: 1;
		border-radius: 0;
		font-size: 14px;
	}
</style>


