<template>
	<div>
		<div>
			<mt-header fixed title="">
				<router-link to="/" slot="left">
					<mt-button><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
				</router-link>
				<!-- <mt-button icon="more" slot="right"></mt-button> -->
			</mt-header>
		</div>
		<div class="cluetile">
			<mt-field placeholder="" v-model="title"></mt-field>
		</div>
		<div id="cluecont">
			<mt-field rows=10 type="textarea" placeholder="" v-model="content"></mt-field>
		</div>
		<div>
			<mt-cell v-for="item in attachFile" :key=item.id :title="item.fileName">
				<img slot="icon" :src="item.filePath" width="24" height="24" />
			</mt-cell>
		</div>
		<div @click="onFilechoose">
			<mt-cell :title="fileTxt" class="cell_color">
				<img slot="icon" src="../images/othrefiles.png" width="16" height="12">
			</mt-cell>
		</div>
		<div @click="toSelectDepartment">
			<mt-cell :title="orgTxt">
				<img slot="icon" src="../images/depaterment.png" width="16" height="14">
			</mt-cell>
		</div>
		<div @click="toSelectStartTime">
			<!--<span v-model="startTime"></span><span v-model="endTime"></span>-->
			<mt-cell :title="dateRange">
				<img slot="icon" src="../images/datepicker.svg" width="18" height="16">
			</mt-cell>
		</div>
		<div>
			<mt-cell title="">
				<mt-switch v-model="isEffective"></mt-switch>
			</mt-cell>
		</div>
		<div class="commit">
			<mt-button @click.native="addData" type="danger" size="large"></mt-button>
		</div>
		<div>
			<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
		</div>
		<div id="shishixingbuxing"></div>
	</div>
</template>
<script>
	import "../plugins/dyhttpupload/css/custom.css";
	import "../plugins/dyhttpupload/js/chunkupload-1.0.5.js";
	import "../plugins/dyhttpupload/js/json2.js";

	import { Toast } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	export default {
		name: 'selectNew',
		data() {
			return {
				title: '',
				content: '',
				maxleng: 10,
				cellstyle: "font-size: 12px",
				fileTxt: '',
				orgTxt: '',
				orgId: '',
				startTime: '',
				endTime: '',
        dateRange:'',
				isEffective: false,
				attachFile: [],
				actions: [{
						name: '',
						method: function() {
							var serverSettings = {};
							serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
							serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
							serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
							var plugin = $.httpUploadPlugin({
								"uploadAddress": serverSettings.uploadAddress,
								"migrateServer": serverSettings.migrateServer
							});
							plugin.startSelectFile("1", function(result) {
								var result1 = JSON.parse(result);
								var resourceList = [];
								if(this.attachFile != null) {
									resourceList = this.attachFile;
								}
								var resource = {};
								resource.fileName = result1.fileName;
								resource.filePath = serverSettings.streamAddress + result1.migrateFileName; //
								resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
								resource.fileSize = result1.fileSize;
								resourceList.push(resource);
								this.attachFile = resourceList;
							}.bind(this), "", 0);
						}.bind(this)
					},
					{
						name: '',
						method: function() {
							var serverSettings = {};
							serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
							serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
							serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
							var plugin = $.httpUploadPlugin({
								"uploadAddress": serverSettings.uploadAddress,
								"migrateServer": serverSettings.migrateServer
							});
							plugin.startSelectFile("1", function(result) {
								var result1 = JSON.parse(result);
								var resourceList = [];
								if(this.attachFile != null) {
									resourceList = this.attachFile;
								}
								var resource = {};
								resource.fileName = result1.fileName;
								resource.filePath = serverSettings.streamAddress + result1.migrateFileName; //
								resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
								resource.fileSize = result1.fileSize;
								resourceList.push(resource);
								this.attachFile = resourceList;
							}.bind(this), "", 1);
						}.bind(this)
					},
					{
						name: '',
						method: function() {
							var serverSettings = {};
							serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
							serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
							serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
							var plugin = $.httpUploadPlugin({
								"uploadAddress": serverSettings.uploadAddress,
								"migrateServer": serverSettings.migrateServer
							});
							plugin.startSelectFile("1", function(result) {
								var result1 = JSON.parse(result);
								var resourceList = [];
								if(this.attachFile != null) {
									resourceList = this.attachFile;
								}
								var resource = {};
								resource.fileName = result1.fileName;
								resource.filePath = serverSettings.streamAddress + result1.migrateFileName; //
								resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
								resource.fileSize = result1.fileSize;
								resourceList.push(resource);
								this.attachFile = resourceList;
							}.bind(this), "", 2);
						}.bind(this)
					}
				],
				sheetVisible: false
			}
		},

		created: function() {
			var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
			if(clueInfo != null) {
				this.title = clueInfo.title;
				this.content = clueInfo.summary;
				this.clueSourceAddr = clueInfo.sourceAddr;
				this.attachFile = clueInfo.attachFile;
				if(clueInfo.orgId != null) {
					this.orgId = clueInfo.orgId + '';
				}
				if(clueInfo.orgName != null) {
					this.orgTxt = clueInfo.orgName + '';
				}
				this.startTime = clueInfo.startTime;
				this.endTime = clueInfo.endTime;
				this.isEffective = clueInfo.isEffective;
				this.dateRange=clueInfo.startTime+'~'+clueInfo.endTime;
			}
		},
		methods: {
			onFilechoose: function() {
				this.sheetVisible = true;
			},

			addData: function() {
				var status = 101;
				if(this.isEffective){
					status = 102;
				}
				if(this.startTime == '' || this.startTime == '' || this.endTime == '' || this.endTime == ''){
					Toast({
						message: '',
						iconClass: 'icon icon-success'
					});
					return;
				}
				MessageBox.confirm('', '').then(action => {
					this.axios.post('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/addSelect', {
							'isShow': 1,
							'title': this.title,
							'status' : status,
							'userId': '8ca66580fa6b42fe9e625f6202fc2192',
							'user': '',
							'content': this.content,
							'departmentId': this.orgId,
							'department': this.orgTxt,
							'exeStartTime' : this.startTime,
							'exeEndTime' : this.endTime,
							'resourceList': this.attachFile
						})
						.then(function(response) {
							var message = '';
							if(response.data.commonResponse.success){
								message = '';
								window.localStorage.removeItem('clueInfo');
							}
							Toast({
								message: message,
								iconClass: 'icon icon-success'
							});
						}.bind(this));
				});
			},

			toSelectDepartment: function() {
				var clueInfo = {
					title: this.title,
					summary: this.content,
					orgId: this.orgId,
					orgName: this.orgTxt,
					attachFile: this.attachFile,
					startTime: this.startTime,
					endTime: this.endTime,
					isEffective: this.isEffective
				}
				window.localStorage.setItem("clueInfo", JSON.stringify(clueInfo));
				this.$router.push({
					name: 'department'
				})
			},

			toSelectStartTime: function() {
				var clueInfo = {
					title: this.title,
					summary: this.content,
					orgId: this.orgId,
					orgName: this.orgTxt,
					attachFile: this.attachFile,
					startTime: this.startTime,
					endTime: this.endTime,
					isEffective: this.isEffective
				}
				window.localStorage.setItem("clueInfo", JSON.stringify(clueInfo));
				this.$router.push({
					name: 'dateSelect'
				})
			},

			toSelectEndTime: function() {
				var d = new Date();
				var year = d.getFullYear();
    			var month = d.getMonth()+1;
    			var day = d.getDate();
				this.endTime = year+'/'+month+'/'+day;
				alert('')
			},

		}

	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .mint-switch-input:checked + .mint-switch-core {
    border-color: #ef4f4f;
    background-color: #ef4f4f;
  }
</style>
<style scoped>
	::-webkit-input-placeholder {
		/* WebKit browsers */
		color: #999;
	}

	 :-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #999;
	}

	 ::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #999;
	}

	 :-ms-input-placeholder {
		/* Internet Explorer 10+ */
		color: #999;
	}

	.mint-header {
		background-color: #fff;
		color: #333333;
		border-bottom: none;
	}

	.return {
		vertical-align: middle;
		margin-right: 10px;
	}

	.cluetile {
		margin-top: 40px;
	}

	.mint-field.is-textarea .mint-cell-value {
		font-size: 12px!important;
	}

	#cluecont textarea {
		font-size: 12px!important;
	}

	.mint-field .cluelink {
		font-size: 12px!important;
	}

	.commit {
		margin-top: 30px;
		padding: 0 5%;
	}

	.cell_color {
		color: #888!important;
	}

</style>



