<template>
  <div>
    <mt-header fixed title="">
      <mt-button slot="left" @click="goBack()"><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
    </mt-header>
    <mt-search v-model="value" cancel-text="" placeholder=""></mt-search>

  </div>
</template>
<script>
  export default {
    name: 'search',
    data:{

    },
    methods: {
      goBack:function(){
        this.$router.go(-1);
      }
    }
  }
</script>
<style>
  .mint-searchbar-inner{
    border-radius: 20px;
  }
  .mint-searchbar-inner .mintui-search{
    font-size: 20px;
    color: #888;
    margin-left: 4px;
  }
  .mint-searchbar-core{
    background-color: #d9d9d9;
    width: 90%;
  }
  .mint-searchbar-inner{
    background-color: #d9d9d9;
  }
  .mint-searchbar{
    background-color:#fff;
  }
</style>
<style scoped>
  .return{
    vertical-align: middle;
    margin-right: 10px;
  }
  .mint-search{
    width: 100vw;
    padding-top:40px;
    position: fixed;
  }
  .mintui-search:before,.mint-searchbar-inner .mintui-search{
    font-size: 22px;
  }

</style>



