import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import ClueNew from '@/components/clueNew.vue'
import ClueList from '@/components/clueList.vue'
import Org from '@/components/Org'
import Tree from '@/components/tree'
import Department from '@/components/department'
import SelectDetail from '@/components/selectDetail.vue'
import Search from '@/components/search.vue'
import CalendarSelect from '@/components/calendarSelect.vue'
import DateSelect from '@/components/dateSelect.vue'
import SelectNew from '@/components/selectNew.vue'
import UserSelect from '@/components/userSelect.vue'
Vue.use(Router)

export default new Router({
 /* mode: 'history',*/
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path:'/clueNew',
      name:'clueNew',
      component:ClueNew
    },
    {
      path:'/clueList',
      name:'clueList',
      component:ClueList,
      meta: { keepAlive: true }/*,
      children:[
        { path:'selectDetail/:id',
          name:'selectDetail',
          component:SelectDetail
    }]*/
    },
    {
      path:'/selectNew',
      name:'selectNew',
      component:SelectNew
    },
    {
      path:'/tree',
      name:'clue',
      component:Tree
    },
    {
      path:'/department',
      name:'department',
      component:Department
    },
    {
      path:'/org/:id',
      name:'org',
      component:Org
      // beforeEnter: (to, from, next) => {
      // 	alert(11) ;
      //   // ...
      // }
    },
    {
      path:'/selectDetail',
      name:'selectDetail',
      component:SelectDetail
    },
    {
      path:'/search',
      name:'search',
      component:Search
    },
    {
      path:'/calendarSelect',
      name:'calendarSelect',
      component:CalendarSelect
    },
    {
      path:'/dateSelect',
      name:'dateSelect',
      component:DateSelect
    },
    {
    	path:'/userSelect',
    	name:'userSelect',
    	component:UserSelect
    }

  ]
})



