<template>
	<div v-show="isShow" class="selectitem">
		<div class="header">
			<mt-header fixed title="">
				<mt-button slot="left" icon="back" @click="goBack()"></mt-button>
			</mt-header>
		</div>
		<div class="selectContent">
			<mt-field v-model="title" readOnly disableClear></mt-field>
			<mt-field rows=10 type="textarea" v-model="content" disableClear readOnly></mt-field>
			<mt-cell :title="status"></mt-cell>
			<mt-cell :title="departmentName" @click.native="toDepartmentSelect">
				<img slot="icon" src="../images/depaterment.png" width="16" height="14">
			</mt-cell>
			<mt-cell :title="exeStartTime" @click.native="toDateSelect">
				<img slot="icon" src="../images/datepicker.svg" width="16" height="14">
			</mt-cell>
			<mt-cell :title="exeEndTime"></mt-cell>
			<mt-cell :title="charger">
				<img slot="icon" src="../images/charger.svg" width="16" height="14">
			</mt-cell>
			<mt-cell :title="membersName" @click.native="toUserSelect">
				<img slot="icon" src="../images/members.svg" width="16" height="14">
			</mt-cell>
			<mt-cell :title="activity">
				<img slot="icon" src="../images/activity.svg" width="16" height="14">
			</mt-cell>
			<mt-cell :title="keyword">
				<img slot="icon" src="../images/tag.svg" width="16" height="14">
			</mt-cell>
			<mt-cell title="" :value="clueCount">
				<img slot="icon" src="../images/clue.svg" width="16" height="14">
			</mt-cell>
			<mt-cell title="" :value="productCount">
				<img slot="icon" src="../images/cre_resource.svg" width="16" height="14">
			</mt-cell>
			<mt-cell title="" :value="attachCount">
				<img slot="icon" src="../images/attach.svg" width="16" height="14">
			</mt-cell>
			<mt-tabbar fixed>
				<mt-tab-item id="" @click.native="doAudit" v-if="isDisplayAudit">
					<img slot="icon" src="../images/audit-grey.svg"> 
				</mt-tab-item>
				<mt-tab-item id="" @click.native="doEdit()">
					<img slot="icon" src="../images/edit-grey.svg"> 
				</mt-tab-item>
				<mt-tab-item id="" @click.native="doChangeStatus(104)">
					<img slot="icon" src="../images/terminate-grey.svg"> 
				</mt-tab-item>
				<mt-tab-item id="" @click.native="doChangeStatus(103)">
					<img slot="icon" src="../images/complete-grey.svg"> 
				</mt-tab-item>
				<mt-tab-item id="" @click.native="doDelete">
					<img slot="icon" src="../images/delete-grey.svg"> 
				</mt-tab-item>
			</mt-tabbar>
			<!--<mt-cell v-if="1":title="exeStartTime"></mt-cell>
    	<mt-cell v-if="1":title="exeEndTime"></mt-cell>
    	<mt-cell v-if="1":title="charger"></mt-cell>
    	<mt-cell v-if="1":title="status"></mt-cell>
    	<mt-cell v-if="1":title="status"></mt-cell>
    	<mt-cell v-if="1":title="status"></mt-cell>-->
		</div>
		<div>
			<department ref="toshowDepartment" v-if="isShowDepartmentSelect" showTopOrBottom=0 isDetail=true> </department>
		</div>
		<div>
			<dateSelect ref="toshowDate" v-if="isShowDateSelect" showTopOrBottom=1 isDetail=true></dateSelect>
		</div>
		<div>
  			<userSelect ref="toshow"></userSelect>
  		</div>
  		
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	import UserSelect from '@/components/userSelect.vue';
	import Department from '@/components/department.vue';
	import DateSelect from '@/components/dateSelect.vue';

	export default {
		name: 'selectDetail',
		data() {
			return {
				title: '',
				content: '',
				status: '',
				departmentName: '',
				exeStartTime: '',
				exeEndTime: '',
				charger: '',
				members: [],
				membersName: '',
				activity: '',
				keyword: '',
				clueCount: '',
				productCount: '',
				attachCount: '',
				selectTopicGuid: '',
				//
				isDisplayAudit: false,
				isDisplayEdit: false,
				isDisplayTerminate: false,
				isDisplayComplete: false,
				isDisplayDelete: false,
				isShowDepartmentSelect: false,
				isShowDateSelect:false,
				isShow: false
			}
		},

		methods: {
			show: function(id) {
				this.isShow = true;
				this.initData(id);
			},
			initData: function(selectTopicGuid) {
				//console.log(JSON.stringify(this.$route.params))
				//console.log(this.$route.params.selectTopicGuid)
				//var id = this.$route.params.selectTopicGuid
				this.selectTopicGuid = selectTopicGuid;
				let id = selectTopicGuid;
				this.axios.get('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/querySelect?id=' + id).then(function(response) {
					this.title = response.data.title;
					this.content = response.data.content;
					this.status = response.data.status + '';
					this.departmentName = response.data.department + '';
					this.exeStartTime = response.data.exeStartTime;
					this.exeEndTime = response.data.exeEndTime;
					if(response.data.charger != null) {
						this.charger = response.data.charger + '';
					}
					var membersName = '';
					for(var i in response.data.members) {
						if(i == response.data.members.length - 1) {
							membersName += response.data.members[i].membersName;
						} else {
							membersName += response.data.members[i].membersName + ',';
						}
					}
					this.membersName = membersName;
					this.members = response.data.members;
					if(response.data.activity != null) {
						this.activity = response.data.activity + '';
					}
					if(response.data.keyword != null) {
						this.keyword = response.data.keyword + '';
					}
					var clueCount = 0;
					var attachCount = 0;
					for(var i in response.data.resourceList) {
						if(response.data.resourceList[i].resourceClass == '') {
							attachCount++;
						} else {
							clueCount++;
						}
					}
					this.clueCount = clueCount + '';
					this.attachCount = attachCount + '';

					//
					this.isDisplayEdit = true;
					this.isDisplayDelete = true;
					if(response.data.status == 101) {
						this.isDisplayAudit = true;
					} else if(response.data.status == 102) {
						this.isDisplayTerminate = true;
						this.isDisplayComplete = true;
					}
				}.bind(this))
			},
			goBack: function() {
				document.getElementsByTagName("body")[0].setAttribute("style","") ;
        		/*document.getElementsByTagName("html")[0].setAttribute("style","");*/
        		this.$emit('show-change');
				this.isShow = false ;
				this.$parent.container=false;
				/*console.log(this.$parent);
        		this.$parent.removeEventListener("touchmove",this.containerScroll, false);*/
			},
			/**/
			updateSelect: function(formData) {
				formData.id = this.selectTopicGuid;
				this.axios.post('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/addSelect', formData)
					.then(function(response) {
						var message = '';
						if(response.data.commonResponse.success) {
							message = '';
						}
						alert(message)
						Toast({
							message: message,
							iconClass: 'icon icon-success'
						});
					}.bind(this));
			},
			doAudit: function() {
				alert(11)
			},
			doEdit: function() {
				alert(11)
			},
			doChangeStatus: function(status) {
				var formData = {
					status: status
				}
				this.updateSelect(formData);
			},
			doDelete: function() {
				var formData = {
					isDelete: 1
				}
				this.updateSelect(formData);
			},
			toDepartmentSelect: function(){
				this.$refs.toshowDepartment.show();
				this.isShowDepartmentSelect = true;
			},
			toDateSelect: function(){
				this.$refs.toshowDate.show();
				this.isShowDateSelect = true;
			},
			toUserSelect: function(){
				var selectedUser = [];
				for(var i in this.members){
					var selectedUserItem = {
						userId: this.members[i].membersId,
						userName: this.members[i].membersName,
						orgName: this.members[i].orgName,
						isSelect: true
					}
					selectedUser.push(selectedUserItem);
				}
				this.$refs.toshow.show(selectedUser, this.selectTopicGuid);
			},
		},
		components:{
  			UserSelect,
  			Department,
  			DateSelect
  		}
	}
</script>
<style>
	.selectitem {
		position: fixed;
		top: 0;
		background: #FFFFFF;
		width: 102%;
		height: 100%;
		overflow: auto;
		z-index: 9999999;
	}
</style>
<style scoped>
	::-webkit-scrollbar {
		width: 2px;
		/*height: 4px;*/
		background-color: transparent;
	}
	
	.selectContent {
		margin-top: 40px;
		padding-bottom: 55px;
	}
	
	.mint-header {
		background-color: white;
		color: #333333;
	}
</style>


