<template>
  <div>
    <div>
     <mt-header fixed title="">
     <router-link to="/" slot="left">
        <mt-button><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
     </router-link>
     <!-- <mt-button icon="more" slot="right"></mt-button> -->
    </mt-header>
    </div>
    <div class="cluetile">
      <mt-field  placeholder="" v-model="cluetitle" placeholder=""></mt-field>
    </div>
    <div id="cluecont">
      <mt-field  rows=10 type="textarea" placeholder="" v-model="cluecontent"></mt-field>
    </div>
    <div>
      <mt-field  placeholder="" v-model="clueSourceAddr" :attr="{ style: cellstyle }"></mt-field>
    </div>
    <div>
    	<mt-cell v-for="item in attachFile" :key=item.id :title="item.fileName">
    		<img slot="icon" :src="item.filePath" width="24" height="24"/>
    	</mt-cell>
    </div>
    <div @click="onFilechoose">
    <mt-cell :title="fileTxt" class="cell_color">
      <img slot="icon" src="../images/othrefiles.png" width="16" height="12">
    </mt-cell>
    </div>
    <div @click="toSelectDepartment">
    <mt-cell :title="orgTxt">
      <img slot="icon" src="../images/depaterment.png" width="16" height="14">
    </mt-cell>
    </div>
    <div class="commit">
        <mt-button @click.native="addClue" type="danger" size="large"></mt-button>
    </div>
    <div>
      <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
    </div>
    <div id="shishixingbuxing"></div>
  </div>
</template>
<script>
import "../plugins/dyhttpupload/css/custom.css";
import "../plugins/dyhttpupload/js/chunkupload-1.0.5.js";
import "../plugins/dyhttpupload/js/json2.js";

import { Toast } from 'mint-ui';
import { MessageBox } from 'mint-ui';
export default {
  name: 'clueNew',
  data () {
    return {
      cluetitle:'',
      cluecontent:'',
      maxleng:10,
      cellstyle: "font-size: 12px",
      fileTxt:'',
      orgTxt: '',
      orgId: '',
      clueSourceAddr: '',
      attachFile: [],
      actions:[
      {
        name:'',
        method:function(){
        	var serverSettings = {};
					serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
					serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
					serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
					var plugin = $.httpUploadPlugin({
                    "uploadAddress": serverSettings.uploadAddress,
                    "migrateServer": serverSettings.migrateServer
                });
					plugin.startSelectFile("1",function(result){
	        	var result1 = JSON.parse(result);
	        	var resourceList = [];
	        	if(this.attachFile != null){
	        		resourceList = this.attachFile;
	        	}
	        	var resource = {};
	        	resource.fileName = result1.fileName;
	        	resource.filePath = serverSettings.streamAddress+result1.migrateFileName;		//
	        	resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
	        	resource.fileSize = result1.fileSize;
	        	resourceList.push(resource);
	        	this.attachFile = resourceList;
        	}.bind(this),"",0);
        }.bind(this)
      },
      {
        name:'',
        method:function(){
        	var serverSettings = {};
					serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
					serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
					serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
					var plugin = $.httpUploadPlugin({
                    "uploadAddress": serverSettings.uploadAddress,
                    "migrateServer": serverSettings.migrateServer
                });
					plugin.startSelectFile("1",function(result){
	        	var result1 = JSON.parse(result);
	        	var resourceList = [];
	        	if(this.attachFile != null){
	        		resourceList = this.attachFile;
	        	}
	        	var resource = {};
	        	resource.fileName = result1.fileName;
	        	resource.filePath = serverSettings.streamAddress+result1.migrateFileName;		//
	        	resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
	        	resource.fileSize = result1.fileSize;
	        	resourceList.push(resource);
	        	this.attachFile = resourceList;
        	}.bind(this),"",1);
        }.bind(this)
      },
      {
        name:'' ,
        method:function(){
        	var serverSettings = {};
					serverSettings.uploadAddress = 'http://111.198.170.19:8086/upload?remoteRootPath=/mnt/clip/newsupload';
					serverSettings.migrateServer = 'http://111.198.170.19:8082/DYCMigrate/';
					serverSettings.streamAddress = 'http://111.198.170.19:8087/NCmedia/';
					var plugin = $.httpUploadPlugin({
                    "uploadAddress": serverSettings.uploadAddress,
                    "migrateServer": serverSettings.migrateServer
                });
					plugin.startSelectFile("1",function(result){
	        	var result1 = JSON.parse(result);
	        	var resourceList = [];
	        	if(this.attachFile != null){
	        		resourceList = this.attachFile;
	        	}
	        	var resource = {};
	        	resource.fileName = result1.fileName;
	        	resource.filePath = serverSettings.streamAddress+result1.migrateFileName;		//
	        	resource.uploader = '8ca66580fa6b42fe9e625f6202fc2192';
	        	resource.fileSize = result1.fileSize;
	        	resourceList.push(resource);
	        	this.attachFile = resourceList;
        	}.bind(this),"",2);
        }.bind(this)
      }
      ],
      sheetVisible:false
    }
  },

  created: function () {
		var clueInfo = JSON.parse(window.localStorage.getItem("clueInfo"));
		if(clueInfo != null){
			this.cluetitle = clueInfo.title;
			this.cluecontent = clueInfo.summary;
			this.clueSourceAddr = clueInfo.sourceAddr;
			this.attachFile = clueInfo.attachFile;
			if(clueInfo.orgId != null){
				this.orgId = clueInfo.orgId+'';
			}
			if(clueInfo.orgName != null){
				this.orgTxt = clueInfo.orgName+'';
			}
		}
  },
   methods:{
    onFilechoose:function(){
      this.sheetVisible=true;
    },

    addClue:function(){
      MessageBox.confirm('','').then(action => {
        this.axios.post('http://192.168.8.101:8080/ClueService/api/resources',{
         'title':this.cluetitle,
         'summary':this.cluecontent,
         'submitUserName':'',
         'submitUserId':'8ca66580fa6b42fe9e625f6202fc2192',
         'departmentId':this.orgId,
         'departmentName':this.orgTxt,
         'sourceAddr': this.clueSourceAddr,
         'sourceSystemId':'PEOPLE_NEW',
         'attachFiles':this.attachFile,
         'classId':32768
         })
         .then(function(response){
         Toast({
         message: '',
         iconClass: 'icon icon-success'
         });
         }.bind(this));
      });
    },

    toSelectDepartment: function(){
    	var clueInfo = {
    		title: this.cluetitle,
    		summary: this.cluecontent,
    		orgId: this.orgId,
    		orgName: this.orgTxt,
    		sourceAddr: this.clueSourceAddr,
    		attachFile: this.attachFile
    	}
    	window.localStorage.setItem("clueInfo", JSON.stringify(clueInfo));
    	this.$router.push({
	        name: 'department'
	    })
    }

   }

}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ::-webkit-input-placeholder { /* WebKit browsers */
    color:    #999;
  }
  :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color:    #999;
  }
  ::-moz-placeholder { /* Mozilla Firefox 19+ */
    color:    #999;
  }
  :-ms-input-placeholder { /* Internet Explorer 10+ */
    color:    #999;
  }
  .mint-header{
    background-color: #fff;
    color: #333333;
    border-bottom: none;
  }
  .return{
    vertical-align: middle;
    margin-right: 10px;
  }
  .cluetile{
    margin-top: 40px;
  }
  .mint-field.is-textarea .mint-cell-value{
    font-size: 12px!important;
  }
  #cluecont textarea{
    font-size: 12px!important;
  }
  .mint-field .cluelink{
    font-size: 12px!important;
  }
  .commit{
    margin-top: 30px;
    padding: 0 5%;
  }
  .cell_color{
    color:#888!important;
  }
</style>



