<template>
  <div>
  	<div :class="{container:container}" id="container">
    <div>
      <mt-header fixed title="">
        <router-link to="/" slot="left">
          <mt-button><img src="../images/return.png" width="18" height="16" class="return"></mt-button>
        </router-link>
      </mt-header>
    	<mt-navbar v-model="selected" fixed>
	  		<mt-tab-item @click.native="allSelect" id="1"></mt-tab-item>
	  		<mt-tab-item @click.native="mySelect" id="2"></mt-tab-item>
        <a class="file_search" @click="toSearch"><img src="../images/search.png"></a>
	 	</mt-navbar>
      <div class="filterBtn">
        <mt-button type="default" @click="selectBtns(0)">{{departmentName}}</mt-button>
        <mt-button type="default" @click="selectBtns(1)">{{allStates}}</mt-button>
        <mt-button type="default" @click="selectBtns(2)"></mt-button>
      </div>
    </div>
    <div class="selectLists">
	    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
		  <!--<mt-cell v-for="item in list" :title="item.title" to="//github.com" is-link value="" label="">
		  	&lt;!&ndash;<img slot="icon" src="../assets/100x100.png" width="24" height="24">&ndash;&gt;
		  </mt-cell>-->
		  <ul class="daylists">
		    <li v-for="item in list" @click="toDetail(item.id)">
        <!--<li v-for="item in list" @click="toSearch">-->
              <b v-if="item.status==101" class="state treatment"></b>
              <b v-if="item.status==102" class="state isgoing"></b>
              <b v-if="item.status==103" class="state complete"></b>
              <b v-if="item.status==104" class="state shutdown"></b>
          		<!--<img src="../images/othrefiles.png" class="listPic">-->
	    		<p class="listTitle">{{ item.title }}</p>
          		<p class="listDetails">
            		<span>{{item.exeStartTime}}{{item.exeEndTime}}</span>
            		<span>{{item.creater}}</span>
            		<span>{{item.department}}</span>
          		</p>
	    	</li>
		  </ul>
		</mt-loadmore>
		<mt-palette-button content="+" style="right: 10px;display: none;">
	      <div class="my-icon-button"></div>
    </mt-palette-button>
	</div>
  <div>
    <mt-popup v-model="popupVisible" position="top" :modal=true>
     <!-- <mt-cell title="" value=""></mt-cell>-->
      <department v-if="popupModal==0" showTopOrBottom=1 @departmentChosen="departmentChosen"> </department>
      <ul v-if="popupModal==1" class="statelits"><li v-for="option in options" @click="stateSelect(option.text,option.id)">{{option.text}}</li></ul>
      <dateSelect v-if="popupModal==2" showTopOrBottom=1 @dateChosen="dateChosen"></dateSelect>
    </mt-popup>
  </div>
  </div>
  <div>
  	<selectDetail :selectedItem="itemSelected" ref="toshow" v-on:reloadData="loadData(1)" v-on:show-change="divchange"></selectDetail>
  </div>

  </div>
</template>
<script>
import selectDetail from '@/components/selectDetail'
import Department from '@/components/department.vue';
import DateSelect from '@/components/dateSelect.vue';

export default {
  name: 'clueList',
  data () {
    return {
      itemSelected :{},
      list: [],
      selected: '1',
      popupModal:'',
      departVisible:false,
      popupVisible:false,
      dateVisible:false,
      allStates:'',
      start:0,
      container:false,
      departmentId: '',
      departmentName: '',
      departmentChosen: function(departmentIdChosen, departmentNameChosen){
      	this.departmentId = departmentIdChosen;
      	this.loadData(0);
      	this.departmentName = departmentNameChosen;
      	this.popupVisible = false;
  		}.bind(this),
  		status: '',
  		createrId: '',
  		chargerId: '',
  		startTime: '',
  		endTime: '',
  		dateChosen: function(startTimeChosen,endTimeChosen){
  			this.startTime = startTimeChosen+' 00:00';
				this.endTime = endTimeChosen+' 23:59';
      	this.loadData(0);

      	this.popupVisible = false;
  		}.bind(this),
      options: [
        { id:'', text: ''},
        { id:'101', text: ''},
        { id:'102', text: ''},
        { id:'103', text: ''},
        { id:'104', text: ''},
        { id:'105', text: ''}
      ],
      scrollTopValue:0,
      allLoaded:false,
    }
  },
  created: function () {
  	this.loadData(0);
  },
  watch:{
    popupVisible:function () {
      if(this.popupVisible == false){
        document.getElementsByTagName("body")[0].setAttribute("style","");
        document.getElementsByTagName("html")[0].setAttribute("style","");
        /*document.getElementById("container").removeEventListener("touchmove",this.containerScroll, false);*/
      }else if(this.popupVisible==true){
        document.getElementsByTagName("body")[0].setAttribute("style","overflow:hidden;height:100%;");
        document.getElementsByTagName("html")[0].setAttribute("style","overflow:hidden;height:100%;");
      }
    }
  },
   methods:{
     divchange(){
       //this.isShowDiv = true ;
       document.body.scrollTop = this.scrollTopValue ;
     },
      containerScroll:function () {
        event.preventDefault();
      },
     selectBtns:function(selectNum){
       if(this.popupVisible == true){
         if(this.popupModal == selectNum){
           this.popupVisible=false;
           /*document.getElementById("container").removeEventListener("touchmove",this.containerScroll, false);*/
           document.getElementsByTagName("body")[0].setAttribute("style","") ;
           document.getElementsByTagName("html")[0].setAttribute("style","");
         }else {
           /*document.getElementById("container").addEventListener("touchmove",this.containerScroll, false);*/
           document.getElementsByTagName("body")[0].setAttribute("style","overflow:hidden;height:100%;");
           document.getElementsByTagName("html")[0].setAttribute("style","overflow:hidden;height:100%;");
         }
       }else if(this.popupVisible==false){
         this.popupVisible=true;
         /*document.getElementById("container").addEventListener("touchmove",this.containerScroll, false);*/
         document.getElementsByTagName("body")[0].setAttribute("style","overflow:hidden;height:100%;");
         document.getElementsByTagName("html")[0].setAttribute("style","overflow:hidden;height:100%;");
       }
       this.popupModal=selectNum;
//       this.isState=true;
     },
      loadTop: function(OrBottom) {
        // 
        this.loadData(0);
      },
    loadBottom: function(){
        // 
    //	  this.allLoaded = true; 
    		this.start += 5;
    		this.loadData(1);
    //		this.$refs.loadmore.onBottomLoaded();
      },
    toDetail: function(id){
//      this.$router.push({
//          name: 'selectDetail',
//          params: {
//            'selectTopicGuid':id
//          }
//      })

	   this.itemSelected={
	   	'selectTopicGuid':id
	   };
     this.scrollTopValue = document.body.scrollTop;
	   document.getElementsByTagName("body")[0].setAttribute("style","overflow:hidden;height:100%;");
     /* document.getElementsByTagName("html")[0].setAttribute("style","overflow:hidden;height:100%;");*/

      /*document.getElementById("container").addEventListener("touchmove",this.containerScroll, false);*/
	   this.$refs.toshow.show(id);
	   this.container=true;

      },
     toSearch: function(){
        this.$router.push({
          path:'/search'
        })
     },
     departmentSelect: function(){
       /*this.$router.push({
         path:'/department'
       });*/
       this.popupModal=0;
       if(this.popupVisible == true){
         this.popupVisible=false;
       }else if(this.popupVisible==false){
         this.popupVisible=true;
       }
     },
     calendarSelect: function(){
       /*this.$router.push({
         path:'/calendarSelect'
       })*/
       this.popupModal=2;
       if(this.popupVisible == true){
         this.popupVisible=false;
       }else if(this.popupVisible==false){
         this.popupVisible=true;
       }
     },
     stateSelect:function (stateText,state) {
        this.status = state;
        this.loadData(0);

        this.allStates = stateText;
       	this.popupVisible=false;
     },
     allSelect: function(){
     	this.departmentId = '';
     	this.createrId = '';
     	this.chargerId = '';
     	this.status = '';
     	this.departmentName = '';
     	this.allStates = '';
     	this.loadData(0);
     },
     mySelect: function(){
     	this.departmentId = '';
     	this.createrId = '';
     	this.chargerId = '1a3710a164b74911b2348d9c1b085c91';
     	this.status = '';
     	this.departmentName = '';
     	this.allStates = '';
     	this.loadData(0);
     },
     /**/
     loadData: function(topOrBottom){
     				if(topOrBottom == 0){
			  			this.start = 0;
			  			this.allLoaded = false;
			  		}
			     	var data = {
			    		"userId" : '1a3710a164b74911b2348d9c1b085c91',
			    		"relatedOrgs" : ['0A2D05CC-B7E5-45B2-9920-4737885BA33B'],
			    		"unrelatedOrgs" : [],
			  		  "department" : this.departmentId,
			  			"createrId" : this.createrId,
			  			"chargerId" : this.chargerId,
			  			"startTime" : this.startTime,
			  			"endTime" : this.endTime,
			//  		"keyword" : keyword,
			//  		"activityId" : activityId,
			  			"status" : this.status,
			//  		"isDelete" : isDelete,
			//  		"atManId" : atManId,
			//  		"browseAtOrgs" : browseAtOrgs,
			    		"systemId" : 'NEWSCOMMAND',
			//  		"searchContent" : $("#searchValue").val(),
			    		"sortColumn" : 'CREATE_TIME',
			    		"direction" : 'DESC',
			//            		"orderBy" : 'exe_start_time asc',
							"start" : this.start,
							"limit" : 5
				};
			  	this.axios.post('http://192.168.8.101:8080/DYPDNewsCommandWeb/api/querySelectList',data).then(function(response){
			  		if(topOrBottom == 0){
			  			this.list = response.data.records;
			  			this.$refs.loadmore.onTopLoaded();
			  		}else if(topOrBottom == 1){
			  			this.list = this.list.concat(response.data.records);
			  			if(this.start >= response.data.totalCount){
  				 	 		this.allLoaded = true; //
	  				 	}
      				this.$refs.loadmore.onBottomLoaded();
			  		}

			      console.log(response.data.records)
				}.bind(this))
     },
   },
  components:{
  	selectDetail,
    child:{
      template:'<ul class="statelits"><li></li><li></li><li></li></ul>'
    },
    child1:{
      template:'<ul class="statelits"><li>1</li><li>1</li><li>1</li></ul>'
    },
    Department,
    DateSelect
  }

}
</script>
<style>
  .container{
    /*display: none;*/
    z-index:-1;
  }
  ul.statelits li{
    width: 100%!important;
    height:40px;
    line-height: 40px;
    text-align: center;
    list-style: none!important;
    border-bottom: 1px solid #dedede;
  }

</style>
<style scoped>

  .mint-header,.mint-navbar.is-fixed{
    z-index: 9999;
  }
  body{
    margin: 0!important;
  }
  p{
    margin: 0;
  }
  ul{
    margin: 0;
    padding: 0;
  }
  .mint-header{
    background-color: #fff;
    color: #333333;
    border-bottom: 1px solid #cccccc;
  }
  .mint-navbar{
    top:40px;
  }
  .listPic{
    position: absolute;
    left: 15px;
    top: 30px;
  }
  .listTitle{
    word-break: break-all;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    padding-right: 20px;
  }
  .listDetails{
    font-size: 14px;
    color: #888;
    position: absolute;
    bottom:15px;
    padding-right: 20px;
  }
  .listDetails span{
    margin-right: 10px;
  }
  .selectLists{
    margin-top:129px;
  }
  .return{
    vertical-align: middle;
    margin-right: 10px;
  }
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #f85759;
    color: #f85759;
    /*margin-bottom: -3px;*/
    margin-bottom: 0px;
  }
  .daylists{
    padding-left: 0px;
  }
  .daylists li{
    padding: 20px 10px 20px 60px;
    list-style: none;
    height:80px;
    border-bottom: 1px solid #dedede;
    position: relative;
  }
  .mint-tab-item:nth-child(1){
    text-align:right;
    padding-right:10%;
  }
  .mint-tab-item:nth-child(2){
    text-align:left;
    padding-left:10%;
  }
  .file_search{
    width: 24px;
    height: 22px;
    position: absolute;
    right: 20px;
    top: 12px;
  }
  .file_search img{
    width:100%;
    height:100%;
  }
  .filterBtn{
    display:flex;
    position: fixed;
    width: 100%;
    left:0;
    top:88px;
    z-index: 9999;
  }
  .filterBtn button{
    flex:1;
    border-radius: 0;
    font-size: 14px;
  }
  .mint-popup-top{
    top:129px;
    width: 100%;
  }
  .statelits{
    background: #FFFFFF;
    z-index: 6;
    width: 100%;
    /*display:none;*/
  }
  .state{
    width: 30px;
    height:36px;
    position: absolute;
    left: 15px;
    top: 30px;
  }
  .shutdown{
    background: url("../images/shutdown.svg");
    background-size: cover;
  }
  .isgoing{
    background: url("../images/isgoing.svg");
    background-size: cover;
  }
  .treatment{
    background: url("../images/isgoing.svg");
    background-size: cover;
  }
  .complete{
    background: url("../images/isgoing.svg");
    background-size: cover;
  }

</style>



